.slider {
  margin: 0 auto;
  max-width: 940px;
}

.slide_viewer {
  height: 600px;
  overflow: hidden;
  position: relative;
}

.slide_group {
  height: 100%;
  position: relative;
  width: 100%;
}

.slide {
  display: none;
  height: 100%;
  position: absolute;
  width: 100%;
}

.slide:first-child {
  display: block;
}

.slide:nth-of-type(1) {
  background: url(../img/pizza-1.jpg);
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
}

